import React, { Children, Component, useState } from "react";
import { AccordainWrp } from "./general/style/style";
// import { toJS } from "mobx";
import { LHS, RHS, Section } from "./general/style/common";

export default function AccordianItem({ title, children, currentAccOpen, index, changeAccIndex, className, margin, style, accordainWrpClassName}) {
  
  // console.log("AccordianItem: ", toJS(arguments));

  return (
    <Section margin={margin?margin:"16px 16px 10px"}>
      <AccordainWrp className={accordainWrpClassName?accordainWrpClassName:""}>
        <div className="accordionWrap" style={style}>
          <div
            className={className?className:"accordItemHeading"}
            onClick={() => changeAccIndex(index == currentAccOpen ? -1 : index)}
            key={index}
          >
            <LHS>
              <h4>{title}</h4>
            </LHS>
            <RHS>
              <span className={index == currentAccOpen ? "icon dwon" : "icon up"}></span>
            </RHS>
          </div>
          {index == currentAccOpen && children ? <>{children}</> : null}
        </div>
      </AccordainWrp>
    </Section>
  );
}

